import { Component } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { HttpClient, HttpParams } from '@angular/common/http';

interface MailChimpResponse {
  result: string;
  msg: string;
}

@Component({
	selector: 'ce-mailchimp-subscribe',
  templateUrl: './ce-mailchimp-subscribe.html',
  styleUrls: ['./ce-mailchimp-subscribe.scss']
})

export class CEMailchimpSubscribe {
	public submitted = false;
	public mailChimpEndpoint = 'https://gmail.us20.list-manage.com/subscribe/post-json?u=dc689105a468d4f66ddf72f1c&amp;id=29f8943dab&';
	public error = '';

	constructor( private http: HttpClient ) { };

  // reactive form components
	email = new FormControl('', [
		Validators.required,
		Validators.email,
	]);
	name = new FormControl('', [
		Validators.required
	]);

	submit() {
		this.error = '';
		if (this.email.status === 'VALID' && this.name.status === 'VALID') {

			const params = new HttpParams()
				.set('NAME', this.name.value)
				.set('EMAIL', this.email.value)
				.set('b_123abc123abc123abc123abc123abc123abc', ''); // hidden input name

			const mailChimpUrl = this.mailChimpEndpoint + params.toString();

      // 'c' refers to the jsonp callback param key. This is specific to Mailchimp
			this.http.jsonp<MailChimpResponse>(mailChimpUrl, 'c').subscribe(response => {
				if (response.result && response.result !== 'error') {
					this.submitted = true;
				}
				else {
					this.error = response.msg;
				}
			}, error => {
				console.error(error);
				this.error = 'Sorry, an error occurred.';
			});
		}
	}
}
